.alpheios-navbuttons__btn {
  display: block;
  width: 40px;
  height: 40px;
  text-align: center;
  cursor: pointer;
  background: transparent no-repeat center center;
  background-size: contain;
  margin: 10px;
}
.alpheios-navbuttons__btn svg {
  width: 40px;
  height: 40px;
  display: inline-block;
  vertical-align: top;
}

.alpheios-navbuttons__btn,
.alpheios-navbuttons__btn.active:hover,
.alpheios-navbuttons__btn.active:focus {
  fill: #D1D1D0;
  stroke: #D1D1D0;
}

.alpheios-navbuttons__btn:hover,
.alpheios-navbuttons__btn:focus,
.alpheios-navbuttons__btn.active {
  fill: #5BC8DC;
  stroke: #5BC8DC;
}