/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-table-sf__title {
  margin-bottom: 5px;
  padding-left: 30px;
}

.alpheios-clickable {
  cursor: pointer;
}

.alpheios-inflections__not-impl-msg {
  padding: 20px;
  font-size: 0.875rem;
}

.alpheios-inflections__table-ctrl-cont {
  display: flex;
  width: 100%;
  margin-bottom: 0.2rem;
  justify-content: flex-end;
}

.alpheios-inflections__table-ctrl-cell {
  flex-grow: 1;
}

div.alpheios-inflections__table-ctrl-cont div.alpheios-inflections__table-ctrl-cell {
  font-size: 0.875rem;
}

.alpheios-inflections__table-ctrl-btn-cont {
  margin-left: 0.2rem;
}

.auk .uk-button-small.alpheios-inflections__control-btn {
  line-height: 1.6;
  white-space: nowrap;
}

.auk .uk-button-small.alpheios-inflections__control-btn--right {
  margin-left: 0.5rem;
}

.infl-prdgm-tbl {
  display: table;
  border-top: 1px solid gray;
  border-left: 1px solid gray;
  margin-bottom: 30px;
}

.infl-prdgm-tbl__row {
  display: table-row;
}

.infl-prdgm-tbl__cell {
  display: table-cell;
  padding: 2px 5px;
  border-right: 1px solid gray;
  border-bottom: 1px solid gray;
  min-width: 20px;
}

.infl-prdgm-tbl-cell--label {
  font-weight: 700;
}

.infl-table {
  display: grid;
  border-left: 1px solid #111;
  border-bottom: 1px solid #111;
  margin-bottom: 1rem;
}

.infl-table--wide {
  /* Data flow order: number- case - declension - gender - type*/
  grid-auto-flow: row;
  grid-template-columns: repeat(21, 1fr);
  /* Default value, will be redefined in JS if necessary */
}

.infl-table--narrow {
  /* Data flow order: declension - number- case - gender - type*/
  grid-auto-flow: row;
  grid-template-columns: repeat(6, 1fr);
  /* Default value, will be redefined in JS if necessary */
}

.infl-table.hidden {
  display: none;
}

.infl-table-narrow-views-cont {
  display: flex;
  flex-wrap: wrap;
}

.infl-cell {
  font-size: 12px;
  padding: 0 2px 0 5px;
  border-right: 1px solid #111;
  border-top: 1px solid #111;
  position: relative;
}

.infl-cell.hidden {
  display: none;
}

.infl-cell--hdr {
  font-weight: 700;
  text-align: center;
}

.infl-cell--hdr .infl-cell__conj-stem {
  text-transform: none;
}

.infl-cell--fw {
  grid-column: 1/-1;
  font-style: italic;
  text-transform: capitalize;
}

.infl-cell.infl-cell--sep {
  height: 50px;
}

.infl-cell--sp0 {
  display: none;
}

.infl-cell--sp1 {
  grid-column-end: span 1;
}

.infl-cell--sp2 {
  grid-column-end: span 2;
}

.infl-cell--sp3 {
  grid-column-end: span 3;
}

.infl-cell--sp4 {
  grid-column-end: span 4;
}

.infl-cell--sp5 {
  grid-column-end: span 5;
}

.infl-cell--sp6 {
  grid-column-end: span 6;
}

.infl-cell--sp7 {
  grid-column-end: span 7;
}

.infl-cell--sp8 {
  grid-column-end: span 8;
}

.infl-cell--sp9 {
  grid-column-end: span 9;
}

.infl-cell--sp10 {
  grid-column-end: span 10;
}

.infl-cell--sp11 {
  grid-column-end: span 11;
}

.infl-cell--sp12 {
  grid-column-end: span 12;
}

.infl-cell--sp13 {
  grid-column-end: span 13;
}

.infl-cell--sp14 {
  grid-column-end: span 14;
}

.infl-cell--sp15 {
  grid-column-end: span 15;
}

.infl-cell--sp16 {
  grid-column-end: span 16;
}

.infl-cell--sp17 {
  grid-column-end: span 17;
}

.infl-cell--sp18 {
  grid-column-end: span 18;
}

.infl-cell--sp19 {
  grid-column-end: span 19;
}

.infl-cell--sp20 {
  grid-column-end: span 20;
}

.infl-cell--sp21 {
  grid-column-end: span 21;
}

.infl-cell--sp22 {
  grid-column-end: span 22;
}

.infl-cell--sp23 {
  grid-column-end: span 23;
}

.infl-cell--sp24 {
  grid-column-end: span 24;
}

.infl-cell--hl {
  background: lightgray;
}

.infl-cell--morph-match,
.infl-table .infl-cell.infl-cell--morph-match {
  border: 3px solid #bce6f0;
}

.infl-cell__conj-stem {
  text-transform: none;
}

.infl-suff {
  cursor: pointer;
}

.row-title-cell {
  text-transform: capitalize;
}

.infl-suff.infl-suff--suffix-match {
  background-color: #bce6f0;
}

.infl-suff--full-match {
  background-color: lightgray;
}

.infl-suff.infl-suff--suffix-match.infl-suff--full-match {
  background-color: #F6D918;
  font-weight: 700;
}