.alpheios-panel h2,
.alpheios-panel h3,
.alpheios-panel h4,
.alpheios-panel h5,
.alpheios-panel h6,
.alpheios-panel p,
.alpheios-panel span,
.alpheios-panel a {
  font-family: inherit;
  background: transparent;
  color: #333333;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  padding: 0;
  font-style: normal;
}
.alpheios-popup button,
.alpheios-popup input,
.alpheios-popup optgroup,
.alpheios-popup select,
.alpheios-popup textarea,
.alpheios-panel button,
.alpheios-panel input,
.alpheios-panel optgroup,
.alpheios-panel select,
.alpheios-panel textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
.alpheios-popup button,
.alpheios-popup select,
.alpheios-panel button,
.alpheios-panel select {
  text-transform: none;
}
.alpheios-popup button,
.alpheios-popup [type=button],
.alpheios-popup [type=reset],
.alpheios-popup [type=submit],
.alpheios-panel button,
.alpheios-panel [type=button],
.alpheios-panel [type=reset],
.alpheios-panel [type=submit] {
  -webkit-appearance: button;
}
.alpheios-popup textarea,
.alpheios-panel textarea {
  overflow: auto;
}
.alpheios-popup [type=checkbox],
.alpheios-popup [type=radio],
.alpheios-panel [type=checkbox],
.alpheios-panel [type=radio] {
  box-sizing: border-box;
  padding: 0;
}
.alpheios-popup b,
.alpheios-panel b {
