  padding: 0;
  text-align: inherit;
  font-style: inherit;
}
.alpheios-popup u,
.alpheios-panel u {
  margin: 0;
  padding: 0;
  vertical-align: inherit;
  font-size: inherit;
  font-weight: inherit;
  text-decoration: inherit;
  color: inherit;
  line-height: inherit;
}

