div.alpheios-checkbox-block {
  display: inline-block;
  position: relative;
  overflow: hidden;
  line-height: 22px;
  max-width: 235px;
  vertical-align: top;
}

.alpheios-checkbox-block {
  /*Unhide on the checked state*/
  /*Adding focus styles on the outer-box of the fake checkbox*/
}
.alpheios-checkbox-block input[type=checkbox] {
  opacity: 0;
  position: absolute;
  left: -999px;
}
.alpheios-checkbox-block label {
  position: relative;
  display: inline-block;
  user-select: none;
  vertical-align: top;
  /*16px width of fake checkbox + 6px distance between fake checkbox and text*/
  padding-left: 22px;
  padding-top: 2px;
}
.alpheios-checkbox-block label::before, .alpheios-checkbox-block label::after {
  position: absolute;
  content: "";
  /*Needed for the line-height to take effect*/
  display: inline-block;
}
.alpheios-checkbox-block label::before {
  height: 16px;
  width: 16px;
  border: 1px solid;
  left: 0px;
  /*(24px line-height - 16px height of fake checkbox) / 2 - 1px for the border
   *to vertically center it.
   */
  top: 3px;
}
.alpheios-checkbox-block label::after {
  height: 5px;
  width: 9px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
  left: 4px;
  top: 7px;
}
.alpheios-checkbox-block input[type=checkbox] + label::after {
  content: none;
}
.alpheios-checkbox-block input[type=checkbox]:checked + label::after {
  content: "";
}
.alpheios-checkbox-block input[type=checkbox]:focus + label::before {
  outline: #3b99fc auto 5px;
}

.alpheios-checkbox-small label {
  font-size: 90%;
  padding-left: 18px;
  line-height: 100%;
}
.alpheios-checkbox-small label::before {
  height: 12px;
  width: 12px;
}

