/************ UIKit vars ************************/
/************ Multiselect vars ************************/
/*********** Font styles ***********************/
/********** Light Schema ***********************/
/********** Dark Schema ***********************/
.alpheios-lookup__form {
  margin: 15px 10px 5px;
  text-align: left;
}

.uk-input.alpheios-lookup__input {
  width: 70%;
  margin-bottom: 10px;
  vertical-align: top;
}
.uk-input.alpheios-lookup__input:focus {
  border-color: #5BC8DC;
}

.uk-button.alpheios-lookup__button {
  vertical-align: top;
  display: block;
}

.alpheios-lookup__settings {
  text-align: left;
}

a.alpheios-lookup__settings-link {
  display: block;
  padding-top: 5px;
}

.alpheios-lookup__button_with_link {
  width: 29%;
}

.alpheios-panel .alpheios-lookup__form {
  width: 100%;
  margin: 5px 0;
}

.alpheios-panel__options-item {
  max-width: none;
}

.alpheios-panel__options-item .uk-select:not([multiple]):not([size]),
.alpheios-panel__options-item .uk-select[multiple],
.alpheios-panel__options-item .uk-select[size],
.alpheios-panel__options-item .uk-textarea {
  max-width: 250px;
}

.alpheios-override-lang {
  margin-bottom: 10px;
}

.alpheios-override-lang__label {
  padding-bottom: 10px;
}